  color: #ffffff;
  font-size: 10px;
  position: absolute;
  left: 0;
  top:0;
  bottom: 0;
  z-index: 9999;
  margin:auto auto;
  right: 0;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.processing:before,
.processing:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%
}
.processing:before {
  left: -3.5em;
  -webkit-animation-delay: -0.32s;
